受心跳和呼吸，把不安的躁动和低落的情绪压在心底，轻轻的安\n          抚，远离世俗的惊扰，安逸一份平淡的心性。没有心事的神伤，常常会制造一种不知所以的挤压与彷徨，困扰心情的也\n          许不是得失，而是一种不知名的感伤，一种灵魂的空旷。而我们常在这种感觉中无法驾驭自我，不经意间就触摸到了一\n          种不知名的脆弱。也许只有安静才能给予心灵极致的净化，才能走出自己设下的漩涡。\n        ")]),_c('p'),_vm._v(" "),_c('p',{staticClass:"textimg"},[_c('img',{attrs:{"src":"http://www.lmlblog.com/blog/13/images/1.jpg","alt":""}})]),_vm._v(" "),_c('p',[_vm._v("\n          我们在一段时光里笑过、哭过，一时排解不开纠结的情绪，随着时光的流逝慢慢沉淀在心底，汇集成了一道雕琢的风景。\n          岁月掠夺了我们的容颜，却留下了属于我们的故事。有些故事虽然只是刹那芳华，却趟过了岁月的心河，融入了血液。在\n          与时光的对望中，嫣然了笑容，泪流了感动，经年走过，我们驻足、回望，却依然心存流连、无怨无悔。\n        ")]),_vm._v(" "),_c('p',[_vm._v("\n          总有一段时光孤单了一段路程，或许有很多话想说，却又不清楚该说什么，该向谁说。也许会莫名的失落，做一些违反常\n          态的举动证明自己存在着。也许喜欢上了孤独，习惯了默默的去承受一切。其实人生并不孤单，只是被某一处阴影遮住了\n          阳光的灼热，而忽略了关心我们的每一双眼睛。四季流转，不同的风景给生命做着无私的衬托，原来每一份关心都来自情\n          怀默默。旅途无期，一声珍重，千言万语的感动。一簇小草，一株野花，一片枯叶，无不彰显生命的厚重。人生本不悲凉，\n          拉开紧固的衣襟，敞开胸怀去接纳阳光，拥抱每一处暖心的风景，热度就会贯穿整个血脉，生命就会充满温情。\n        ")]),_vm._v(" "),_c('p',[_vm._v("\n          一个人静静的听一首歌，品一杯茶，在空灵幽雅的旋律中舒展所有的心事，抛开人情的冷凉，以一曲歌韵释怀所有的心伤。\n          当时光掠过发髻，轻舞衣衫，抖落满身微尘雾雨，所有的快乐和忧伤都无我无关。让心如止水，以一朵野菊花的姿态默守\n          一份淡淡然。任流年荡过指尖，不提及往事，不言论悲欢。\n        ")]),_vm._v(" "),_c('p',{staticClass:"textimg"},[_c('img',{attrs:{"src":"http://www.lmlblog.com/blog/13/images/3.jpg","alt":""}})]),_vm._v(" "),_c('p',[_vm._v("\n          岁月就像跳动的音符，穿过季节的斑驳，奏响着时光的章程，谱曲着一首首生命之歌。在一份寂静中沉淀自己的心性，细细倾\n          听一首轻柔的音乐，任旋律穿透肺腑融入血脉。什么都不去想，一切烦乱被剥落，岁月仿佛静止，时光在这一刻凝固。心随旋\n          律飘飞，荡游在太空，穿越在无我的幻境中，无争无扰、无欲无求。闭上眼睛，感受心跳和呼吸，醉了。胸怀瞬息被放大，包\n          容了自己，包容了世界，释怀了繁杂。一条河流过，流进生命，穿过了风尘，趟过了岁月，轻轻地、轻轻地流淌着。\n        ")]),_vm._v(" "),_c('p',[_vm._v("\n          拨开渐退的雾霾，面对晨起的朝阳，舒展怀抱盈一缕温暖。踏入一席清新之地，赏心满目青绿，取一滴晨露品其甘甜，野花轻\n          吐嫣蕊低眉浅笑，池柳轻摇衣带飘飘。绿草随风踏浪，风过风停静默一份淡泊。小桥流水清澈依然，环山而绕清逸而悠远，看\n          流云隐逸远山的羞涩，倾听鸟儿嬉戏云水间。任时光走过指尖，将晴好尽数打点，拾一份温润入心，守一份安然随情。\n        ")]),_vm._v(" "),_c('p',[_vm._v("\n          踏过冰雪的轮回，脱下旧日薄凉，嗅着春天的气息，播下一粒快乐的种子，用温润孕育让它结满温情，风起风停笑对人生，月\n          缺月圆淡看旅程，孤单的日子，拾起一缕明媚，与温暖同行。旅途匆匆紧握一份感动，沧海桑田珍重一路随行，生命的价值是\n          一种紧握，风雨路上依旧芳菲，人生的完美是携手冷暖，同看落日余晖，与时光安然相处，默守岁月赋予的唯美……\n        ")])]),_vm._v(" "),_c('div',{staticClass:"txtfoot"},[_c('a',{attrs:{"href":"#"}},[_vm._v("\n          阅读全文\n        ")]),_vm._v(" "),_c('a',{attrs:{"href":"#"}},[_vm._v("\n          评论\n        ")]),_vm._v(" "),_c('a',{attrs:{"href":"#"}},[_vm._v("\n          转载\n        ")])])]),_vm._v(" "),_c('div',{staticClass:"pages"},[_c('span',[_vm._v("1")]),_vm._v(" "),_c('a',{attrs:{"href":"#"}},[_vm._v("2")]),_vm._v(" "),_c('a',{attrs:{"href":"#"}},[_vm._v("3")]),_vm._v(" "),_c('a',{staticClass:"next",attrs:{"href":"#"}},[_vm._v("下一页")])])])}]
