    text-decoration: underline;
  }
  .music audio {
    width: 100%;
    padding-top: 10px;
  }
  .blogitem {
    width: 73%;
    float: right;
    background: #fff;
  }
  .blogitem article {
    /*border-bottom: 30px solid #F4F3EF;*/
  }
  .title {
    background:5% 80%;
    font-size: 20px;
    line-height: 28px;
    display: inline-block;
    padding: 38px 60px 8px 80px;
    border-bottom: 1px solid #e2e2e2;
    width: 100%;
  }
  .title a {
    color: #454545;
  }
  .title a:hover {
    padding: 20px
  }
  .text {
    font-size: 14px;
    padding: 25px 30px;
  }
  .text p {
    margin-bottom: 10px;
    line-height: 1.5em;
    font: 14px "宋体";
    text-indent: 2em;
  }
  .textimg {
    text-align: center;
  }
  .textimg img {
    max-width: 500px;
  }
  .txtfoot {
    padding: 20px 60px;
    border-top: 1px solid #dddcdb;
    text-align: right;
    box-shadow: #999 2px 2px 3px;
  }
  .txtfoot a {
    margin-left: 10px;
    color: #A6A6A6;
    font-size: 12px;
  }
  .txtfoot a:hover {
    color: #000;
  }
  .pages {
    padding: 25px 60px 25px;
    background: #F4F3EF;
    text-align: right;
  }
  .pages span {
    padding: 5px 8px;
    border: none;
    margin: 0 0 0 5px;
    background: #936639;
    color: #fff;
  }
  .pages a {
    background: #fff;
    color: #454545;
    padding: 5px 8px;
    border: none;
    margin: 0 0 0 5px;
  }
  .pages a:hover {
    background: #936639;
    color: #fff;
  }
</style>



